.footer-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #333;
  margin: -1em;
  margin-top: 1em;
  padding: 1em;
}

.footer-content .main-menu-footer {
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding-bottom: 1em;
  border-bottom: 1px solid #444;
}

.footer-content .main-menu-footer a {
  color: lightgrey;
  font-size: 0.95em;
}

.footer-content .main-menu-footer a:hover {
  color: grey;
  text-decoration: none;
}

.other-footer-content-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.other-footer-content-wrapper .site-brand-footer {
  line-height: normal;
  padding: 1em 0em;
  color: #666;
  font-weight: bold;
}

.other-footer-content-wrapper .site-brand-footer .company-name {
  font-size: 1.5em;
}

.other-footer-content-wrapper .other-footer-links {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.other-footer-content-wrapper .other-footer-links a {
  color: lightgrey;
  font-size: 0.85em;
}

.other-footer-content-wrapper .other-footer-links a:hover {
  color: grey;
  text-decoration: none;
}

.site-author {
  display: flex;
  justify-content: center;
  color: #666;
  font-size: .8em;
}

.site-author a {
  color: #999;
}
